.section_testimonials_hero {
  padding: 100px 0;
  transform: skewY(-5deg);
  .container {
    transform: skewY(5deg);
  }
  .section-title{
    padding-bottom: 50px;
  }
  .swiper {
    .swiper-slide {
      position: relative;
      background-color: var(--dark-color);
      color: var(--light-color);
      text-align: center;
      font-size: 16px;
      width: 480px;
      height: 480px;
      box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
      border-radius: 50%;
      img {
        width: 100%;
        height: 100%;
        opacity: 0.7;
        border-radius: 50%;
      }
      figcaption {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate3d(-50%, -50%, 0);
        &::before {
          font-family: "FontAwesome";
          content: "\201C";
          position: absolute;
          font-size: 170px;
          opacity: 0.35;
          font-style: normal;
          -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
          top: -60%;
          left: 50%;
        }
        blockquote {
          padding: 0;
          margin: 0;
          font-size: 24px;
          @include media('<ml') {
            font-size: 18px;
          }
        }
        h3 {
          text-transform: uppercase;
          font-size: 28px;
          font-weight: 400;
          margin-top: 10px;
          @include media('<ml') {
            font-size: 20px;
          }
        }
      }
      @include media('<ml') {
        width: 320px;
        height: 320px;
      }
    }
  }
  .swiper-3d .swiper-slide-shadow-left,
  .swiper-3d .swiper-slide-shadow-right {
    background-image: none;
  }
}
